@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Montserrat&display=swap);
html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "Montserrat", sans-serif;
  background-image: url(/static/media/circuitry.6f9cdad0.png);
}

.beautify-body {
  background-image: url(/static/media/computerloop.ade91928.gif);
}

#root {
  height: 100%;
}

a {
  color: inherit;
  text-decoration: inherit;
}

h1 {
  margin: 0;
  padding: 0;
}

p {
  margin-left: 20px;
  margin-right: 20px;
}

.heading {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.box {
  padding: 30px;
  margin: 0 20% 0 20%;
  background: white;
  border: 2px solid black;
  font-size: 20px;
}

@media only screen and (max-width: 875px) {
  .box {
    margin: 0 15% 0 15%;
  }

  p {
    font-size: 18px;
  }

  .beautify-body {
    background-size: 250px 250px;
  }
}

@media only screen and (max-width: 700px) {
  p {
    font-size: 17px;
  }
}

@media only screen and (max-width: 625px) {
  .box {
    margin: 0 10% 0 10%;
  }

  h1 {
    font-size: 30px;
  }

  h2 {
    font-size: 25px;
  }

  h3 {
    font-size: 20px;
  }
}

@media only screen and (max-width: 540px) {
  p {
    font-size: 15px;
  }
}

@media only screen and (max-width: 500px) {
  .box {
    padding: 30px 10px 30px 10px;
  }
  p {
    margin-left: 10px;
    margin-right: 10px;
  }
}

@media only screen and (max-width: 395px) {
  h1 {
    font-size: 20px;
  }

  h2 {
    font-size: 18px;
  }

  h3 {
    font-size: 16px;
  }
}

@media only screen and (max-width: 300px) {
  .box {
    margin: 0 5% 0 5%;
  }

  p {
    font-size: 12px;
  }
}

#About {
  height: 160px;
}

#tom {
  width: 130px;
  border: 2px solid black;
  border-radius: 100px;
  margin: 20px 0 10px 0;
}

#technologies {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 40px 0 40px;
}

i {
  margin-bottom: 5px;
}

.technology {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 20px;
}

@media only screen and (max-width: 830px) {
  .technology {
    font-size: 15px;
  }
}

@media only screen and (max-width: 625px) {
  #technologies {
    margin: 0 20px 0 20px;
  }

  .technology {
    font-size: 12px;
    margin: 20px 10px 20px 10px;
  }
}

@media only screen and (max-width: 625px) {
  #About {
    height: 130px;
  }

  #tom {
    width: 80px;
  }
}

#Contact {
  height: 123px;
}

#contact-form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 472px;
  width: 400px;
  margin: 20px auto 0 auto;
  padding: 15px 30px 20px 30px;
  display: flex;
  flex-direction: column;
  background: lightpink;
  border: 2px solid black;
  box-shadow: 5px 5px gray;
}

input {
  border-radius: 50px;
  border: 2px black solid;
  padding: 5px 10px 5px 10px;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}

input:focus {
  outline: none;
}

textarea {
  height: 250px;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  padding: 20px;
  border: 2px black solid;
  border-radius: 20px;
}

textarea:focus {
  outline: none;
}

button {
  align-self: center;
  width: 160px;
  background: white;
  border: 2px solid black;
  font-family: "Josefin Sans", sans-serif;
  font-size: 15px;
  padding: 14px 0 10px 0;
  font-style: italic;
}

button i {
  margin-right: 5px;
}

button:hover {
  cursor: pointer;
}

button:focus {
  outline: none;
}

::-webkit-input-placeholder {
  font-style: italic;
}

:-ms-input-placeholder {
  font-style: italic;
}

::placeholder {
  font-style: italic;
}

@media only screen and (max-width: 850px) {
  #contact-form {
    width: 300px;
  }
}

@media only screen and (max-width: 500px) {
  #contact-form {
    width: 200px;
    height: 350px;
    box-shadow: 3px 3px gray;
  }

  input,
  textarea {
    font-size: 12px;
  }

  textarea {
    height: 170px;
  }

  button {
    width: 100px;
    font-size: 12px;
    padding: 8px 0 2px 0;
    font-style: italic;
  }
}

@media only screen and (max-width: 360px) {
  #contact-form {
    padding: 15px 10px 20px 10px;
  }

  textarea {
    height: 170px;
  }
}

#Header-wrap {
  position: absolute;
}

#Header {
  display: flex;
  flex-direction: column;
  position: fixed;
  z-index: 1;
  width: 100%;
}

#top {
  font-family: "Josefin Sans", sans-serif;
  display: flex;
  background-color: black;
  color: white;
  padding: 13px 0 13px 0;
  align-items: center;
}

#title {
  margin-left: 40px;
  font-size: 35px;
}

#links {
  display: flex;
  font-size: 12px;
  margin-left: auto;
  margin-right: 20px;
}

.link {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
}

#navbar {
  display: flex;
  padding: 10px 0 10px 0;
  font-size: 20px;
  background: lightpink;
  color: black;
  font-weight: bold;
  align-items: center;
  border-bottom: 2px solid black;
}

.nav {
  margin-left: 40px;
  margin-right: 30px;
}

#beautify {
  margin-left: auto;
  margin-right: 50px;
  display: flex;
  align-items: center;
}

#beautify label {
  font-size: 20px;
  margin-right: 10px;
  font-style: italic;
}

input[type="checkbox"] {
  zoom: 1.5;
  margin: 0;
}

@media only screen and (max-width: 700px) {
  #title {
    font-size: 30px;
  }
}

@media only screen and (max-width: 625px) {
  #title {
    font-size: 25px;
  }

  #links {
    font-size: 10px;
  }

  #navbar {
    font-size: 15px;
  }

  #beautify label {
    font-size: 15px;
  }
}

@media only screen and (max-width: 540px) {
  .link {
    margin-right: 10px;
  }

  .nav {
    margin-right: 0px;
  }

  #beautify {
    margin-right: 20px;
  }

  #big-screen {
    display: none;
  }
}

@media only screen and (max-width: 470px) {
  #navbar {
    font-size: 12px;
  }

  #beautify label {
    font-size: 12px;
  }
}

@media only screen and (max-width: 425px) {
  #title {
    font-size: 20px;
    margin-left: 20px;
  }
  #links {
    font-size: 8px;
    margin-right: 10px;
  }
  #navbar {
    padding-left: 10px;
  }
  .nav {
    margin-left: 10px;
    margin-right: 10px;
  }
}

@media only screen and (max-width: 340px) {
  .nav {
    font-size: 12px;
    margin-left: 4px;
    margin-right: 4px;
  }

  #beautify label {
    margin-right: 3px;
  }
}

@media only screen and (max-width: 300px) {
  #title {
    font-size: 17px;
  }
}

#Projects {
  height: 123px;
}

.heading {
  text-align: center;
}

#note {
  font-size: 15px;
  margin-bottom: 5px;
}

#project-gallery {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.project {
  margin: 10px;
  position: relative;
  border: 2px solid black;
  height: 186px;
  width: 350px;
}

.description-hide {
  opacity: 0;
}

.description-show {
  top: 0;
  opacity: 1;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 186px;
  width: 350px;
  background: rgb(255, 192, 203, 0.9);
  transition: all 0.4s ease 0s;
}

.description-beautify {
  top: 0;
  opacity: 1;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 186px;
  width: 350px;
  background: yellow;
}

.project-title {
  font-family: "Josefin Sans", sans-serif;
  text-decoration: underline;
  font-size: 25px;
}

.project-description {
  margin: 0 10px 0 10px;
  text-align: center;
  font-size: 17px;
}

.project-technologies {
  font-family: "Josefin Sans", sans-serif;
  font-size: 18px;
}

.project-links {
  display: flex;
  font-size: 14px;
}

.project-links a {
  display: flex;
  font-weight: bold;
  border: 2px solid black;
  background: white;
  padding: 5px 16px 4px 16px;
  margin: 0 5px 0 5px;
}

.project-links i {
  margin-right: 5px;
}

@media only screen and (max-width: 540px) {
  #note {
    font-size: 12px;
  }
}

@media only screen and (max-width: 480px) {
  #project-gallery img,
  .project,
  .description-show,
  .description-beautify {
    height: 140px;
    width: 263px;
  }

  .project-title {
    font-size: 20px;
  }

  .project-description {
    font-size: 13px;
  }
  .project-technologies {
    font-size: 14px;
  }

  .project-links {
    font-size: 13px;
  }

  .project-links a {
    display: flex;
    font-weight: bold;
    border: 2px solid black;
    background: white;
    padding: 3px 16px 2px 16px;
    margin: 0 5px 0 5px;
  }
}

@media only screen and (max-width: 350px) {
  #project-gallery img,
  .project,
  .description-show,
  .description-beautify {
    height: 105px;
    width: 197px;
  }

  .project-title {
    font-size: 15px;
  }

  .project-description {
    font-size: 10px;
  }
  .project-technologies {
    font-size: 11px;
  }

  .project-links {
    font-size: 11px;
  }

  .project-links a {
    display: flex;
    font-weight: bold;
    border: 2px solid black;
    background: white;
    padding: 2px 16px 1px 16px;
    margin: 0 3px 0 3px;
  }
}

